import React from 'react';
import './index.scss';

const questions = [
  {
    title: '   ',
    variants: [' ', '  ', '   '],
    correct: 0,
  },
  {
    title: '   ?',
    variants: [' ', ' 1  100 ', ' 100  200 '],
    correct: 1,
  },
  {
    title: '   -   ',
    variants: [
      ' ',
      ' ',
      ' ',
    ],
    correct: 2,
  },
];

function Result({correct}) {
  return (
    <div className="result">
      <img className='img' alt='#' src="https://cdn-icons-png.flaticon.com/512/2278/2278992.png" />
      <h2 className='h2'>  {correct}   {questions.length}</h2>
      <a className='a' href='/'>
      
      </a>
      
    </div>
  );
}

function Game({ step, question, onClickVariant}) {
  const percentage = Math.round((step / questions.length) * 100);
  

  return (
    <>
      <div className="progress">
        <div style={{ width: `${percentage}%` }} className="progress__inner"></div>
      </div>
      <h1 className='h1'>{question.title}</h1>
      <ul className='ul'>
      {question.variants.map((text, index) => <li className='li' onClick={()=>onClickVariant(index)} key={text}>{text}</li>)}
      </ul>
    </>
  );
}

function Quiz() {
  const [step, setStep] = React.useState(0);
  const [correct, setCorrect] = React.useState(0);
  const question = questions[step];
  
  const onClickVariant = (index) => {
    console.log(step, index);
    setStep(step + 1);
    if (index === question.correct) {
      setCorrect(correct + 1);
    }
  }
  return (
    <div className="Quiz">
     {
      step !== questions.length ?  (
        <Game step = {step} question = {question} onClickVariant= {onClickVariant}/>
        ) : (
          <Result correct={correct} />
        )}
      
    </div>
  );
}

export default Quiz;
